import React,{ useState,useRef,useEffect } from 'react';
import firebase from './../../firebase';
import moment from 'moment';
import Fileupload from './fileupload';

import $ from 'jquery';

const AddPost = (props) => {
  const [allPosts,setAllPosts] = useState([]);
  const [allMyPosts,setAllMyPosts] = useState([]);
  const [countPosts,setCountPosts] = useState(0);
  const title = useRef(null);
  const description = useRef(null);
  const content = useRef(null);

  const bgstyle={
    backgroundColor: '#282c34',
    color: 'white'
  }

  const button={
    borderRadius: '15px',
    margin: '0 0 15px 0',
  }

  useEffect(() => {
    //alert("Show Posts");
    firebase.database().ref("/posts").orderByChild('id')
      .on('value', snapshot => {
          let posts = [];
          let myposts = [];
          snapshot.forEach(child => {
          var data = child.val();
          posts.push({id: data.id,title: data.title,description: data.description,content: data.content,user_id: data.user_id,image: data.image,time: data.time});
          if(data.user_id == localStorage.getItem("user_id")){
          myposts.push({id: data.id,title: data.title,description: data.description,content: data.content,user_id: data.user_id,image: data.image,time: data.time});
          }
          });
          //setAllPosts(posts);
          setCountPosts(posts.length);
          setAllMyPosts(myposts);
      });
      if(countPosts>2){
        $(".container-fluid").css("position","relative");
      }else{
        $(".container-fluid").css("position","fixed");
      }
  })

  function addpost(){
    if(title.current.value.length>1 && description.current.value.length>1){
      var curtime = moment().format("DD/MM/YYYY HH:mm:ss");
      var refdb = firebase.database().ref('/posts').push();
        refdb = refdb.toString().slice(-20);
        setTimeout(function(){ 
        let updateData = firebase.database().ref("/posts").child(refdb);
        updateData.child("id").set(countPosts+1);
        updateData.child("user_id").set(localStorage.getItem("user_id"));
        updateData.child("title").set(title.current.value);
        updateData.child("description").set(description.current.value);
        updateData.child("content").set(content.current.value);
        updateData.child("image").set(localStorage.getItem("image_url"));
        updateData.child("time").set(curtime);
      }, 300);
      setCountPosts(countPosts+1);
      alert("Add post done!");
    } else {
      alert("Please fill the Title and Description fields!");
    }
  }

  return (
      <div className="container" style={bgstyle}>
        <h2>This is Add Posts Page</h2>
        <div className="col-6">
        <div className="form-group">
          <label htmlFor="title" className="sr-only">Title</label>
          <input ref={title} type="text" id="title" className="form-control" placeholder="Title" name="title" required autoFocus />
          <div className="valid-feedback"></div>
          <div className="invalid-feedback">Please fill out this field.</div>
        </div>
        <div className="form-group">
          <label htmlFor="description" className="sr-only">Description</label>
          <input ref={description} type="text" id="description" className="form-control" placeholder="Description" name="description" required autoFocus />
          <div className="valid-feedback"></div>
          <div className="invalid-feedback">Please fill out this field.</div>
        </div>
        <div className="form-group">
          <label htmlFor="content" className="sr-only">Content</label>
          <textarea ref={content} type="text" id="content" className="form-control" placeholder="content" name="content" required autoFocus />
          <div className="valid-feedback"></div>
          <div className="invalid-feedback">Please fill out this field.</div>
        </div>
        <Fileupload />
        <button className="btn btn-primary" type="submit" style={button} onClick={addpost}>Add</button>
        </div>
        {allMyPosts.map((item, index) => (<div className="col-6">
          <p className="text-left">{item.title}</p>
          <p className="text-left">{item.description}</p>
          <p className="text-left">{item.content}</p>
          <p className="text-left">{item.time}</p></div>))}
      </div>
    );
}

export default AddPost;