import React,{ useState,useRef,useEffect } from 'react';
import firebase from './../../firebase';

import $ from 'jquery';

const home = function Home() {
  const [allPosts,setAllPosts] = useState([]);
  const [countPosts,setCountPosts] = useState(0);

  const bgstyle={
    backgroundColor: '#282c34',
    color: 'white'
  }
  
  const carousel={
    marginBottom: '50px'
  }

  const posts={
    marginBottom: '50px',
    color: 'black'
  }

  const allposts={
    marginBottom: '50px'
  }

  useEffect(() => {
    //alert("Show Posts");
    firebase.database().ref("/posts").orderByChild('id')
      .on('value', snapshot => {
          let posts = [];
          snapshot.forEach(child => {
          var data = child.val();
          posts.push({id: data.id,title: data.title,description: data.description,content: data.content,user_id: data.user_id,image: data.image,time: data.time});
          });
          setAllPosts(posts);
          setCountPosts(posts.length);
      });
      $(".container-fluid").css("position","relative");
  })

  function moredetails(id){
    //alert("Mode details"+id);
    window.location.href = "/reactblog/#/viewpost/"+id;
  }

  return (
      <div className="container" style={bgstyle}>
        <h1>Home</h1>

        <div id="demo" className="carousel slide" data-ride="carousel" style={carousel}>
            <ul className="carousel-indicators">
                <li data-target="#demo" data-slide-to="0" className="active"></li>
                <li data-target="#demo" data-slide-to="1"></li>
                <li data-target="#demo" data-slide-to="2"></li>
            </ul>
            <div className="carousel-inner">
                <div className="carousel-item active">
                <img src="https://www.w3schools.com/bootstrap4/la.jpg" alt="Los Angeles"/>
                </div>
                <div className="carousel-item">
                <img src="https://www.w3schools.com/bootstrap4/chicago.jpg" alt="Chicago"/>
                </div>
                <div className="carousel-item">
                <img src="https://www.w3schools.com/bootstrap4/ny.jpg" alt="New York"/>
                </div>
            </div>
            <a className="carousel-control-prev" href="#demo" data-slide="prev">
                <span className="carousel-control-prev-icon"></span>
            </a>
            <a className="carousel-control-next" href="#demo" data-slide="next">
                <span className="carousel-control-next-icon"></span>
            </a>
        </div>
        <div style={allposts}>
        {allPosts.map((item, index) => (<div className="card col-6 mx-auto" style={posts}>
          <div className="card-body">
          <img class="img-fluid" src={"https://firebasestorage.googleapis.com/v0/b/reactblog-824c0.appspot.com/o/image%2F"+item.image+"?alt=media"} />
          <p>{item.title}</p>
          <p>{item.description}</p>
          <p>Created at: {item.time}</p>
          <p>Author: {item.user_id}</p>
          <button className="btn btn-primary" onClick={()=>moredetails(item.id)}>More Details</button></div></div>))}
          </div>
      </div>
    );
}

export default home;