import React,{ useRef,useState,useEffect } from 'react';
import { useParams} from 'react-router-dom';
import firebase from './../../firebase';
import moment from 'moment';

import $ from 'jquery';

const viewpost = function Viewpost() {
  const [allPosts,setAllPosts] = useState([]);
  const [countPosts,setCountPosts] = useState(0);

  const bgstyle={
    backgroundColor: '#282c34',
    color: 'white'
  }

  const align={
    left: '0'
  }

  let { id } = useParams();

  useEffect(() => {
    firebase.database().ref("/posts").orderByChild('id')
      .on('value', snapshot => {
          let posts = [];
          snapshot.forEach(child => {
          var data = child.val();
          if(data.id==id){
          posts.push({id: data.id,title: data.title,description: data.description,content: data.content,user_id: data.user_id,time: data.time});
          }
          });
          setAllPosts(posts);
          setCountPosts(posts.length);
      });
    $(".container-fluid").css("position","fixed");
  })
  
  return (
      <div className="container" style={bgstyle}>
        <h2>This is Viewpost Page</h2>
        <p>{id}</p>
        {allPosts.map((item, index) => (<div className="col-6">
          <p className="text-left" style={align}>{item.title}</p>
          <p className="text-left" style={align}>{item.description}</p>
          <p className="text-left" style={align}>{item.content}</p>
          <p className="text-left" style={align}>Created at: {item.time}</p>
          <p className="text-left" style={align}>Author: {item.user_id}</p></div>))}
      </div>
    );
}

export default viewpost;